.commdBranch {
    display: flex;

    justify-content: center;
    align-items: center;
    position: absolute;
    width: 70%;
    height: 90%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid black;
    background-color: rgba(37, 37, 37, 0.7);
    transition: background-color 0.2s ease-in-out;
}

.commdBranch:hover {
    background-color: rgba(37, 37, 37, 0.8);
}

.graphInfo {
    position: absolute;
    top: 10px;
    left: 10px;
    color: white;
    font-size: 100%;
    z-index: 1;
}